@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,700;0,800;1,100;1,200;1,300;1,400&display=swap');


*{
  font-family: 'Poppins', sans-serif;
}

.modal {
  backdrop-filter: blur(8px);
  width: 500px;
  height: 550px;
  position: absolute;
  margin-left: -225px;
  margin-top: -275px;
}
.modal > .header {
  width: 500px;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 500px;
  padding: 10px 5px;
}

.modal > .close {
  color:black;
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}

